const Image = ({ in: show, onExited, src }) => {
  const { opacity } = useSpring({
    from: { opacity: 1 },
    to: show ? {} : { opacity: 0 },
    onRest: () => onExited(),
  });

  return (
    <animated.div
      className={styles.Image}
      style={{
        position: "absolute",
        opacity,
        background: `url(${src}) center / cover no-repeat`,
      }}
    ></animated.div>
  );
};

const FACTOR = 1;

const Gallery = ({ images, className }) => {
  const [active, setActive] = useState(0);

  const t = useRef();

  const wait = () =>
    console.log(["wait"], t.current) ||
    clearTimeout(t.current) ||
    setTimeout(next, 2000 * FACTOR);
  const plan = () =>
    console.log(["plan"], t.current) ||
    clearTimeout(t.current) ||
    setTimeout(next, 1500);
  const next = () => {
    t.current = plan();
    setActive((active) =>
      console.log(["next"], active) || active === null
        ? 0
        : (active + 1) % images.length
    );
  };

  useEffect(() => {
    t.current = wait();
    return () => {
      clearTimeout(t.current);
    };
  }, []);

  const onEnter = (key) => {
    t.current = setActive(key) || wait();
  };

  return (
    <div className={cx(styles.Gallery, className)}>
      <TransitionGroup>
        <Image key={active} src={images[active]} />
      </TransitionGroup>
      {images.length === 1 ? null : (
        <div className={styles.Navi}>
          {images.map((src, key) => (
            <div
              key={key}
              className={cx(styles.Item, active === key && styles.active)}
              onMouseEnter={(e) => onEnter(key)}
            ></div>
          ))}
        </div>
      )}
    </div>
  );
};

const Preview = ({ in: show, onExited, images, name, info, path }) => {
  const { opacity, left } = useSpring({
    from: { opacity: 0, left: 0 },
    delay: show ? 100 : 0,
    to: show ? { opacity: 1, left: 100 } : { opacity: 0, left: 0 },
    config: { mass: 1, tension: 170, friction: show ? 30 : 20 },
    onRest: onExited,
  });

  return (
    <animated.div
      style={{
        opacity,
        transform: left.interpolate(
          (left) => `translateX(${(5 * (100 - left)) / 100}%)`
        ),
      }}
      className={styles.Preview}
    >
      <a href={`#${path}`} className={styles.Text}>
        <div className={styles.Name}>{name}</div>
        {info && (
          <div className={styles.Info}>
            {info.map(({ icon, text }, key) => (
              <div key={key} className={styles.Item}>
                {icon && <Icon icon={ICONS[icon]} />} {text}
              </div>
            ))}
          </div>
        )}
        {images && <Gallery className={styles.Images} images={images} />}
        <div className={styles.Button}>Zobacz ofertę</div>
      </a>
    </animated.div>
  );
};

const Summary = ({ in: show, onExited, summary }) => {
  const { opacity, top } = useSpring({
    from: { opacity: 0, top: 0 },
    delay: show ? 100 : 0,
    to: show ? { opacity: 1, top: 100 } : { opacity: 0, top: 0 },
    config: { mass: 1, tension: 170, friction: show ? 30 : 20 },
    onRest: onExited,
  });

  return (
    <animated.div
      style={{
        opacity,
        transform: top.interpolate(
          (top) => `translateY(${(5 * (100 - top)) / 100}%)`
        ),
      }}
      className={styles.Summary}
    >
      <Format>{summary}</Format>
    </animated.div>
  );
};

export const ITALY_TEMPLATES = {
  [TEMPLATE_POPUP]: ({
    parent,
    links,
    text,
    background,
    overlay,
    map,
    items = [],
  }) => {
    const { opacity, top } = useSpring({
      from: { opacity: 0, top: 100 },
      delay: 100,
      to: { opacity: 0.8, top: 0 },
      config: { mass: 1, tension: 120, friction: 30 },
    });

    return (
      <div className={styles.TemplatePopup}>
        <div
          style={{
            position: "relative",
            width: "100vw",
            height: "100vh",
            overflow: "hidden",
            background: `url(${background}) center / cover no-repeat`,
          }}
          onClick={(e) => {
            const { x, y, width, height } = e.target.getBoundingClientRect();
            console.log(
              ((e.clientX - x) / width) * 100,
              ((e.clientY - y) / height) * 100
            );
          }}
        >
          {parent && (
            <a href={`#${parent}`} className={styles.Back}>
              <Icon
                icon={ICONS.X}
                width="24"
                height="24"
                viewBox="0 0 768 768"
              />
            </a>
          )}
          {links && (
            <div className={styles.Links}>
              {links.map(({ icon, text, path }, key) => (
                <a
                  key={key}
                  href={`#${path}`}
                  className={styles.Icon}
                  alt={text}
                >
                  <Icon icon={ICONS[icon]} width="24" height="24" />
                </a>
              ))}
            </div>
          )}
          {text && (
            <animated.div
              style={{
                opacity,
                transform: top.interpolate(
                  (top) => `translateY(${(10 * top) / 100}%)`
                ),
              }}
              className={styles.Text}
            >
              <Format>{text}</Format>
            </animated.div>
          )}
        </div>
      </div>
    );
  },
  [TEMPLATE_ITALY]: ({
    background,
    parent,
    overlay,
    summary,
    map,
    items = [],
  }) => {
    const [active, setActive] = useState(null);
    const [view, setView] = useState(VIEW_MAP);

    const t = useRef();

    const wait = () =>
      console.log(["wait"], t.current) ||
      clearTimeout(t.current) ||
      setTimeout(next, 10000 * FACTOR);
    const plan = () =>
      console.log(["plan"], t.current) ||
      clearTimeout(t.current) ||
      setTimeout(next, 5000);
    const next = () => {
      t.current = plan();
      setActive((active) =>
        console.log(["next"], active) || active === null
          ? 0
          : (active + 1) % items.length
      );
    };

    useEffect(() => {
      t.current = wait();
      return () => {
        clearTimeout(t.current);
      };
    }, []);

    const onEnter = (key) => {
      t.current = setActive(key) || wait();
    };

    return (
      <div className={styles.TemplateItaly}>
        <div
          style={{
            position: "relative",
            width: "100vw",
            height: "100vh",
            overflow: "hidden",
            background: `url(${(view === VIEW_INTRO) && active !== null ? items[active].images[0]: background}) center / cover no-repeat`,
          }}
          onClick={(e) => {
            const { x, y, width, height } = e.target.getBoundingClientRect();
            console.log(
              ((e.clientX - x) / width) * 100,
              ((e.clientY - y) / height) * 100
            );
          }}
        >
          {parent && (
            <a href={`#${parent}`} className={styles.Back}>
              <Icon
                icon={ICONS.ARROW_LEFT}
                width="16"
                height="16"
                viewBox="0 0 768 768"
              />
              Powrót
            </a>
          )}
          {summary && (
            <div className={styles.Menu}>
              <a
                href="#"
                className={cx(view === VIEW_MAP && styles.active)}
                onClick={(e) =>
                  e.preventDefault() || setActive(null) || setView(VIEW_MAP)
                }
              >
                <Icon icon={ICONS.MAP} /> Mapa
              </a>
              <a
                href="#"
                className={cx(view === VIEW_INTRO && styles.active)}
                onClick={(e) =>
                  e.preventDefault() || setActive(null) || setView(VIEW_INTRO)
                }
              >
                <Icon icon={ICONS.VIDEO} /> Prezentacja
              </a>
              <a
                href="#"
                className={cx(view === VIEW_SUMMARY && styles.active)}
                onClick={(e) =>
                  e.preventDefault() || setActive(null) || setView(VIEW_SUMMARY)
                }
              >
                <Icon icon={ICONS.CLIPBOARD} /> Opis
              </a>
              <a
                href="#"
                onClick={(e) => e.preventDefault()}
                className={styles.Cta}
              >
                Rezerwuj
              </a>
            </div>
          )}
          <TransitionGroup>{view !== VIEW_INTRO && <Map />}</TransitionGroup>
          <svg style={{ width: "100%", height: "100%", position: "absolute" }}>
            {view !== VIEW_INTRO &&
              items.map(({ path, x, y }, key) => (
                <a key={key} href={`#${path}`}>
                  <circle
                    className={cx(
                      styles.circle,
                      active === key && styles.active
                    )}
                    cx={`${x}%`}
                    cy={`${y}%`}
                    onMouseEnter={(e) => onEnter(key)}
                  />
                </a>
              ))}
            {((items) =>
              items.length > 0 ? (
                <>
                  <rect
                    x="10%"
                    y="20%"
                    width="1"
                    height="70%"
                    className={styles.Timeline}
                  />

                  {items.map(({ path, x, y }, key) => (
                    <a key={key} href={`#${path}`}>
                      <circle
                        className={cx(
                          styles.dot,
                          active === key && styles.active
                        )}
                        cx={`${10}%`}
                        cy={`${y}%`}
                        onMouseEnter={(e) => onEnter(key)}
                      />
                    </a>
                  ))}
                </>
              ) : null)(items.filter(({ time }) => Boolean(time)))}
          </svg>
          <TransitionGroup>
            {active !== null &&
              ((props) => <Preview key={active} {...props} />)(items[active])}
          </TransitionGroup>
          {summary && (
            <TransitionGroup>
              {view === VIEW_SUMMARY && <Summary summary={summary} />}
            </TransitionGroup>
          )}
        </div>
      </div>
    );
  },
};
