import React from "react";
import { Provider, useSelector } from "react-redux";
import { applyMiddleware, createStore } from "redux";
import {createActions,
  handleActions,
} from "redux-actions";
import thunk from "redux-thunk";
// import cx from "classnames";

import styles from "./App.module.scss";
import { useSpring, animated } from "react-spring";
import { TransitionGroup } from "react-transition-group";

import { createBrowserHistory } from "history";

import { ITALY_TEMPLATES } from "./components/Italy";
import { ITALY } from "./data/index";

const locationHash = (locationHash) => (dispatch) => {
  const [path, hash = "/italy/"] =
    decodeURI(locationHash).match(/^#(.+)/) || [];
  console.log(["LOCATION_HASH"], path, hash);
  dispatch(navigatePath(hash));
};

// eslint-disable-next-line
const { navigate, navigatePath, back } = createActions({
  NAVIGATE: (path) => {
    console.log(["navigate"], path);
    history.push(`#${path}`);
  },
  NAVIGATE_PATH: (path) => path,
  BACK: () => {
    history.goBack();
  },
});

const reducer = handleActions(
  new Map([
    [
      navigatePath,
      (state, { payload: current }) => ({
        ...state,
        history: [...state.history, state.current],
        current,
      }),
    ],
  ]),
  {
    playing: false,
    timeout: 1000,
    counter: 0,
    current: "/",
    history: [],
    content: {},
    view: {
      ...ITALY,
    },
  }
);

const store = createStore(reducer, applyMiddleware(thunk));

const history = createBrowserHistory({
  hashType: "slash",
});

console.log(["history.location"], history.location.hash);
store.dispatch(locationHash(history.location.hash));

// eslint-disable-next-line
const unlisten = history.listen((location, action) => {
  // location is an object like window.location
  console.log(["history.listen"], action, location.hash, location.state);
  store.dispatch(locationHash(location.hash));
});

const Wrapper = ({ in: show, onExited, children, ...props }) => {
  const { opacity } = useSpring({
    from: { opacity: 1 },
    to: show ? {} : { opacity: 0 },
    onRest: () => onExited(),
  });

  return (
    <animated.div
      style={{
        position: "absolute",
        opacity,
      }}
      className={styles.Wrapper}
    >
      {children}
    </animated.div>
  );
};

const Main = () => {
  const path = useSelector((state) => state.current);
  const { name, related, template, ...view } = useSelector(
    (state) => state.view[path]
  );
  const content = {
    path,
    name,
    ...useSelector((state) => state.content[related]),
    ...view,
  };
  const Template = {
    ...ITALY_TEMPLATES,
  }[template];
  console.log(["content"], content);
  return content ? (
    <div className={styles.Main}>
      <TransitionGroup>
        <Wrapper key={path} path={path}>
          <Template {...content}>
            <pre className={styles.Pre}>{JSON.stringify(content, null, 2)}</pre>
          </Template>
        </Wrapper>
      </TransitionGroup>
    </div>
  ) : null;
};

const App = () => {
  return (
    <Provider store={store}>
      <Main />
    </Provider>
  );
};

export default App;
